* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.container-login {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    padding: 12px;
    background-color: rgb(219, 219, 219);

    .navbar-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 10vh;

        .links {
            display: flex;
            flex: 0.75;
            height: 100%;
            justify-content: center;
            align-items: center;

            .nav-item {
                display: flex;
                flex: 0.75;
                justify-content: center;
                align-items: center;

                a {
                    color: black;
                    text-decoration: none;
                    font-size: 20px;

                    &:hover {
                        color:#007bff;
                    }
                }
            }
        }

        .logo {
            display: flex;
            flex: 0.5;
            justify-content: center;
            align-items: center;
            height: 100%;
            color:#007bff;

            h1 {
                display: flex;
                flex: 1;
                justify-content: center;
                align-items: center;
                color: black;
            }
        }

        .empty {
            display: flex;
            flex: 0.75;
            height: 100%;
            justify-content: center;
            align-items: center;
        }
    }
    .main-content {
        display: flex;
        flex-direction: column;
        align-content: space-evenly;
        justify-content: flex-start;
        flex: 1;
        padding: 36px;
        .main-title {
            display: flex;
            justify-content: center;
            text-align: center;
            flex: 0.5;
        }
        .main-btn {
            display: flex;
            justify-content: center;
            text-align: center;
            .btn-primary {
                font-size: 1.5rem;
            }
        }
        .main-preview {
            display: flex;
            justify-content: center;
            text-align: center;
            flex: 1.5;
            margin: auto;
            width: 50%;
            .preview {
                display: flex;
                flex: 1;
                background-image: url("/dist/images/map-preview.png");
                background-position: center;
                background-size: contain;
                margin-bottom: 24px;
            }
        }
    }
}
